<template>
  <section class="ui-image-box">
    <img :src="src"/>
    <b class="iconfont icon-close btnClose" @click="handDelete"></b>
  </section>
</template>

<script>
  export default {
    name: 'image-box',
    props: {
      src: {
        type: String,
        require: false,
        default: ''
      }
    },
    data () {
      return {}
    },
    create () {

    },
    methods: {
      handDelete () {
        this.$emit('delete')
      },
    }
  }
</script>

<style lang="scss" scoped>
  .ui-image-box {
    width: 110px;
    height: 110px;
    border: solid #e5e5e5 1px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      display: block;
    }
    .btnClose {
      background-color: rgba(0, 0, 0, 0.5);
      text-align: center;
      font-weight: 400;
      font-size: 8px;
      color: #fff;
      width: 20px;
      height: 20px;
      position: absolute;
      right: 0;
      top: 0;
    }
  }
</style>



