<template>
  <section class="file-image2">
    <template v-if="src">
      <image-box :src="src" @delete="handDelete"></image-box>
    </template>
    <template v-else>
      <section class="ui-upload">
        <b class="iconfont icon-add"></b>
        <input
          ref="file"
          type="file"
          accept="image/jpeg, image/png, image/jpg, image/gif"
          :multiple="multiple"
          @click="handClick"
          @change="handChange"/>
      </section>
    </template>
  </section>
</template>

<script>
  import ImageBox from '@/components/image-box.vue'

  export default {
    name: 'file-image2',
    components: {
      ImageBox
    },
    props: {
      multiple: {
        type: Boolean,
        default: true
      },
      src: {
        type: String,
        require: false,
        default: ''
      }
    },
    data () {
      return {}
    },
    create () {

    },
    methods: {
      handClick () {
        this.$refs.file.value = ''
      },
      handChange (event) {
        console.log(event)
        let files = event.target.files
        this.$emit('change', event)
        if (!files.length) return
        for (let i = 0; i < files.length; i++) {
          let fr = new FileReader()
          fr.onload = event => {
            console.log(event)
            this.$emit('load', event, files[i])
          }
          fr.readAsDataURL(files[i])
        }
      },
      handDelete () {
        this.$emit('delete')
      },
    }
  }
</script>

<style lang="scss" scoped>
  .file-image2 {
    display: inline-block;
  }

  .ui-upload {
    text-align: center;
    color: #e5e5e5;
    width: 110px;
    height: 110px;
    border: solid #e5e5e5 1px;
    position: relative;
    overflow: hidden;
    .iconfont {
      line-height: 26.4vw;
      font-size: 30px;
    }
    input[type="file"] {
      width: 100%;
      z-index: 9;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      opacity: 0;
    }
  }
</style>



